<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 添加按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog">添加更多</el-button>
        </el-col>
      </el-row>
      <!-- 表格区域 -->
      <tree-table
        class="tree-table"
        :data="catelist"
        :columns="columns"
        show-index
        index-text="#"
        border
        :expand-type="false"
        :selection-type="false">
        <!-- 是否有效 -->
        <template slot="isok" slot-scope="scope">
          <i v-if="scope.row.cat_deleted" class="el-icon-error" style="color: red"></i>
          <i v-else class="el-icon-success" style="color: lightgreen"></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag size="mini" v-else-if="scope.row.cat_level === 1" type="success">二级</el-tag>
          <el-tag size="mini" v-else-if="scope.row.cat_level === 2" type="warning">三级</el-tag>
        </template>
        <!-- TODO: 操作 -->
        <template slot="opt" slot-scope="">
          <el-button size="mini" class="el-icon-edit" type="primary">编辑</el-button>
          <el-button size="mini" class="el-icon-delete" type="danger">删除</el-button>
        </template>
      </tree-table>
      <!-- 分页按钮区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!-- 这是添加分类的对话框 -->
    <el-dialog
      @close="addCateDialogClosed"
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%">
      <el-form ref="addCateFormRef" :model="addCateForm" :rules="addCateFormRules" label-width="80px">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <!-- 选择分类级联菜单 -->
          <el-cascader
            @change="parentCateChanged"
            clearable
            v-model="selectedKeys"
            :options="parentCatelist"
            :props="casCaderProps"></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'cate',
  data() {
    return {
      // 查询商品分类的数据列表的条件
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 商品分类的数据列表
      catelist: [],
      // 商品分类的总数
      total: 0,
      // 表格的绑定规则
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name',
          headerAlign: 'center'
        },
        {
          label: '是否有效',
          type: 'template',
          template: 'isok',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '排序',
          type: 'template',
          template: 'order',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '操作',
          type: 'template',
          template: 'opt',
          align: 'center',
          headerAlign: 'center'
        }
      ],
      // 控制添加分类对话框的显示
      addCateDialogVisible: false,
      // 添加分类的表单数据
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      // 添加分类的表单的绑定规则
      addCateFormRules: {
        cat_name: { required: true, message: '请输入分类名称', trigger: 'blur' }
      },
      // 父级分类列表
      parentCatelist: [],
      // 级联选择器的数据绑定规则
      casCaderProps: {
        expandTrigger: 'hover',
        checkStrictly: true,
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 添加分类时的父级分类
      selectedKeys: []
    }
  },
  created() {
    this.getCatelist()
  },
  methods: {
    // 获取商品分类的数据列表
    async getCatelist() {
      // 发送请求
      const { data: res } = await this.$http.get('/categories', { params: this.queryInfo })
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 绑定数据
      this.catelist = res.data.result
      this.total = res.data.total
      // console.log(res)
    },
    // 监听分页长度改变
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCatelist()
    },
    // 监听分页页码改变
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCatelist()
    },
    // 展示添加分类的对话框
    showAddCateDialog() {
      // 获取父级分类列表
      this.getParentCatelist()
      // 显示对话框
      this.addCateDialogVisible = true
    },
    // 获取父级分类列表
    async getParentCatelist() {
      // 发送请求
      const { data: res } = await this.$http.get('/categories', { params: { type: 2 } })
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      this.parentCatelist = res.data
      // console.log(res.data)
    },
    // 监听级联菜单值的变化
    parentCateChanged() {
      const len = this.selectedKeys.length
      if (len === 0) return
      this.addCateForm.cat_level = len
      this.addCateForm.cat_pid = this.selectedKeys[len - 1]
      // console.log(this.selectedKeys)
    },
    // 添加分类
    addCate() {
      // console.log(this.addCateForm.cat_name)
      // 表单验证
      this.$refs.addCateFormRef.validate(async valid => {
        // 表单验证失败
        if (!valid) return
        // 发送请求
        const { data: res } = await this.$http.post('/categories', this.addCateForm)
        // 请求失败
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        // 刷新表格
        this.getCatelist()
        // console.log(res.data)
      })
      // 关闭对话框
      this.addCateDialogVisible = false
    },
    // 监听添加分类对话框的关闭
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm = {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      }
    }
  }
}
</script>

<style lang="less" scoped>
.tree-table {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
