<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 警告 -->
      <el-alert
        show-icon
        :closable="false"
        title="注意：只允许为第三级分类设置相关参数"
        type="warning">
      </el-alert>
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类：</span>
          <!-- 级联选择框 -->
          <el-cascader
            v-model="selectedCateKeys"
            :options="catelist"
            :props="cascaderProps"
            @change="cascaderChange">
          </el-cascader>
        </el-col>
      </el-row>
      <!-- tab 页签区域 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!-- 动态参数面板 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button :disabled="isBtnDisabled" type="primary" size="mini" @click="addDialogVisible = true">添加参数</el-button>
          <!-- 表格 -->
          <el-table :data="manyTableData" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="handleTagClose(i, scope.row)">{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="参数名称" prop="attr_name"></el-table-column>
            <!-- 操作 -->
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" class="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
                <el-button size="mini" type="danger" class="el-icon-delete" @click="removeParamsById(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 静态属性面板 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button :disabled="isBtnDisabled" type="primary" size="mini" @click="addDialogVisible = true">添加属性</el-button>
          <!-- 表格 -->
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="handleTagClose(i, scope.row)">{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="属性名称" prop="attr_name"></el-table-column>
            <!-- 操作 -->
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" class="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">编辑</el-button>
                <el-button size="mini" type="danger" class="el-icon-delete" @click="removeParamsById(scope.row.attr_id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加参数/属性的对话框 -->
    <el-dialog
      @close="addDialogClosed"
      :title="'添加' + titleText"
      :visible.sync="addDialogVisible"
      width="50%">
      <!-- 表单 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 操作 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改参数/属性的对话框 -->
    <el-dialog
      @close="editDialogClosed"
      :title="'添加' + titleText"
      :visible.sync="editDialogVisible"
      width="50%">
      <!-- 表单 -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 操作 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data() {
    return {
      // 分类列表
      catelist: [],
      // 分类级联列表绑定数据的规则
      cascaderProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 级联列表中选择的分类id
      selectedCateKeys: [],
      // tab标签被激活的选项卡
      activeName: 'many',
      // 动态参数的数据
      manyTableData: [],
      // 静态属性的数据
      onlyTableData: [],
      // 控制添加参数/属性的对话框的显示
      addDialogVisible: false,
      // 添加的表单的数据
      addForm: {
        attr_name: ''
      },
      // 表单的验证规则
      addFormRules: {
        attr_name: { required: true, message: '请输入参数/属性', trigger: 'blur' }
      },
      // 控制修改参数/属性的对话框的显示
      editDialogVisible: false,
      // 修改的表单的数据
      editForm: {
        attr_name: ''
      },
      // 修改参数/属性的表单的验证规则
      editFormRules: {
        attr_name: { required: true, message: '请输入参数/属性', trigger: 'blur' }
      }
    }
  },
  created() {
    this.getCatelist()
  },
  methods: {
    // 获取商品分类的数据列表
    async getCatelist() {
      // 发送请求
      const { data: res } = await this.$http.get('/categories')
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 绑定数据
      this.catelist = res.data
      // console.log(res)
    },
    // 监听级联选择框的变化
    cascaderChange() {
      this.getParamsData()
    },
    // tab页签点击事件
    handleTabClick() {
      this.getParamsData()
    },
    // 获取参数数据
    async getParamsData() {
      // 未选择第三级分类
      if (this.selectedCateKeys.length !== 3) {
        // 清空选择的分类
        this.selectedCateKeys = []
        // 清空表格
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // 根据所处面板和分类id发起请求
      const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: this.activeName } })
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 处理vals
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals
          ? item.attr_vals.split(' ')
          : []
        // 控制`new tag`输入框的显示
        item.inputVisible = false
        // 输入框的值
        item.inputValue = ''
      })
      // 绑定数据
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else if (this.activeName === 'only') {
        this.onlyTableData = res.data
      }
      // console.log(res.data)
    },
    // 监听添加参数/属性对话框的关闭
    addDialogClosed() {
      // 重置表单
      this.$refs.addFormRef.resetFields()
    },
    // 添加参数/属性
    addParams() {
      // 表单验证
      this.$refs.addFormRef.validate(async valid => {
        // 验证失败
        if (!valid) return
        // 验证成功，发送请求
        const { data: res } = await this.$http.post(`/categories/${this.cateId}/attributes`, {
          ...this.addForm,
          attr_sel: this.activeName
        })
        // 请求失败
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        // 刷新数据
        // this.getParamsData()
        this.activeName === 'many' ? this.manyTableData.push(res.data) : this.onlyTableData.push(res.data)
        // console.log(res.data)
        // 关闭对话框
        this.addDialogVisible = false
      })
    },
    // 展示编辑对话框
    async showEditDialog(attrId) {
      const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes/${attrId}`, { attr_sel: this.activeName })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      this.editForm = res.data
      // 显示对话框
      this.editDialogVisible = true
    },
    // 监听修改参数/属性对话框的关闭
    editDialogClosed() {
      // 重置表单
      this.$refs.editFormRef.resetFields()
    },
    // 修改参数/属性
    editParams() {
      // 表单验证
      this.$refs.editFormRef.validate(async valid => {
        // 验证失败
        if (!valid) return
        // 验证成功，发送请求
        const { data: res } = await this.$http.put(`/categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
          attr_name: this.editForm.attr_name,
          attr_sel: this.activeName
        })
        // 请求失败
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        // 刷新数据
        this.getParamsData()
        // console.log(res.data)
        // 关闭对话框
        this.editDialogVisible = false
      })
    },
    // 通过id删除参数
    async removeParamsById(attrId) {
      // 弹出确认框
      const confirmResult = await this.$confirm(`此操作将永久删除该${this.titleText}, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 取消删除
      if (confirmResult !== 'confirm') return this.$message.info('删除操作已取消！')
      // 执行删除
      const { data: res } = await this.$http.delete(`/categories/${this.cateId}/attributes/${attrId}`)
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 刷新数据
      this.getParamsData()
    },
    // `new tag`输入框按下回车或失去焦点
    handleInputConfirm(attr) {
      attr.inputValue = attr.inputValue.trim()
      // 输入的是空白字符
      if (attr.inputValue === '') {
        attr.inputValue = ''
        attr.inputVisible = false
        return
      }
      // 含有空白字符
      if (attr.inputValue.indexOf(' ') !== -1) {
        this.$message.error('不能包含空白字符！')
        return
      }
      // 发起添加tag的请求
      attr.attr_vals.push(attr.inputValue)
      this.saveAttrVals(attr, _ => attr.attr_vals.pop())
      attr.inputValue = ''
      // 隐藏输入框
      attr.inputVisible = false
    },
    // 从`new tag`按钮切换到输入框
    showInput(attr) {
      attr.inputVisible = true
      // 获取焦点
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // tag标签关闭时执行删除操作
    handleTagClose(i, attr) {
      // 发起请求
      const temp = attr.attr_vals.splice(i, 1)
      this.saveAttrVals(attr, _ => attr.attr_vals.splice(i, 0, temp))
    },
    // 保存参数/属性到服务器
    async saveAttrVals(attr, cb) {
      const { data: res } = await this.$http.put(`/categories/${this.cateId}/attributes/${attr.attr_id}`, {
        attr_name: attr.attr_name,
        attr_sel: this.activeName,
        attr_vals: attr.attr_vals.join(' ')
      })
      // 请求失败
      if (res.meta.status !== 200) {
        cb && cb()
        return this.$message.error(res.meta.msg)
      }
      // 请求成功
      this.$message.success(res.meta.msg)
    }
  },
  computed: {
    // 控制添加参数和添加属性按钮是否禁用
    isBtnDisabled() {
      return this.selectedCateKeys.length !== 3
    },
    // 选中的第三级分类id
    cateId() {
      return this.selectedCateKeys.length === 3
        ? this.selectedCateKeys[2]
        : null
    },
    // 添加参数/属性的对话框的标题
    titleText() {
      if (this.activeName === 'many') return '动态参数'
      return '静态属性'
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 15px;
}
.input-new-tag {
  width: 120px;
}
</style>
